import React, { Component } from "react";
import { Consumer } from "../../context";
import TextInputGroup from "../layouts/TextInputGroup";
import axios from "axios";
import uuid from "uuid";

class Addfriend extends Component {
  state = {
    name: "",
    email: "",
    phone: "",
    addr: "",
    errors: {}
  };

  onSubmit = async (dispatch, e) => {
    e.preventDefault();

    const { name, email, phone, addr } = this.state;

    // Check For Errors
    if (name === "") {
      this.setState({ errors: { name: "Name is required" } });
      return;
    }

    if (email === "") {
      this.setState({ errors: { email: "Email is required" } });
      return;
    }

    if (phone === "") {
      this.setState({ errors: { phone: "Phone is required" } });
      return;
    }
    if (addr === "") {
      this.setState({ errors: { addr: "Addr is required" } });
      return;
    }

    const newfriend = {
      id: uuid(),
      name,
      email,
      phone,
      addr
    };

    const res = await axios.post(
      "https://jsonplaceholder.typicode.com/users",
      newfriend
    );

    dispatch({ type: "ADD_friend", payload: res.data });

    // Clear State
    this.setState({
      name: "",
      email: "",
      phone: "",
      addr: "",
      errors: {}
    });

    this.props.history.push("/");
  };

  onChange = e => this.setState({ [e.target.name]: e.target.value });

  render() {
    const { name, email, phone, errors, addr } = this.state;

    return (
      <Consumer>
        {value => {
          const { dispatch } = value;
          return (
            <div className="card mb-3">
              <div className="card-header">Add friend</div>
              <div className="card-body">
                <form onSubmit={this.onSubmit.bind(this, dispatch)}>
                  <TextInputGroup
                    label="Name"
                    name="name"
                    placeholder="Enter Name"
                    value={name}
                    onChange={this.onChange}
                    error={errors.name}
                  />
                  <TextInputGroup
                    label="Email"
                    name="email"
                    type="email"
                    placeholder="Enter Email"
                    value={email}
                    onChange={this.onChange}
                    error={errors.email}
                  />
                  <TextInputGroup
                    label="Phone"
                    name="phone"
                    placeholder="Enter Phone"
                    value={phone}
                    onChange={this.onChange}
                    error={errors.phone}
                  />
                  <TextInputGroup
                    label="Addr"
                    name="addr"
                    placeholder="Enter Address"
                    value={addr}
                    onChange={this.onChange}
                    error={errors.addr}
                  />
                  <input
                    type="submit"
                    value="Add friend"
                    className="btn btn-light btn-block"
                  />
                </form>
              </div>
            </div>
          );
        }}
      </Consumer>
    );
  }
}

export default Addfriend;



